.App {
  text-align: center;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App>.item-container{
  height:700px;
  width:100%;
  margin:auto;
}

.App > .item-containerProject{
  height: 1000px;
  width: 100%;
  margin: auto;
}

.App >.item-containerContact{
  height:500px;
  width:100%;
  margin:auto;
}

#chakraIcon{
  width: 50px;
}